<template>
  <div class="search-bar mb-4">
    <div class="card shadow-sm">
      <div class="card-body">
        <h4 class="card-title mb-4 text-primary">üîç</h4>
        <div class="form-group">
          <div class="input-group mb-3">
            <input
              v-model="searchInput"
              type="text"
              class="form-control form-control-lg"
              placeholder="ËæìÂÖ•‰π¶ÂêçÊàñISBNÁ†Å"
              @keyup.enter="handleSearch"
            />
            <button
              @click="handleSearch"
              class="btn btn-primary btn-lg"
              :disabled="!isValidInput"
            >
              <i class="bi bi-search me-1"></i>
              ÊêúÁ¥¢
            </button>
          </div>
          <div v-if="error" class="alert alert-danger mt-2">
            {{ error }}
          </div>
          <small class="form-text text-muted">
            ÊîØÊåÅ‰π¶ÂêçÊàñISBNÁ†ÅÔºà10‰ΩçÊàñ13‰ΩçÊï∞Â≠óÔºâÊêúÁ¥¢
          </small>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'SearchBar',
  data() {
    return {
      searchInput: '',
      error: '',
      currentRequest: null,
      debounceTimeout: null,
      isSearching: false
    };
  },
  computed: {
    isValidInput() {
      return this.searchInput.trim() && !this.error;
    }
  },
  watch: {
    searchInput(newValue) {
      this.validateInput(newValue);
    }
  },
  methods: {
    validateInput(value) {
      const trimmedValue = value.trim();
      if (!trimmedValue) {
        this.error = '';
        return;
      }

      // ISBN validation
      if (/^\d+$/.test(trimmedValue)) {
        if (trimmedValue.length !== 10 && trimmedValue.length !== 13) {
          this.error = 'ISBNÂøÖÈ°ªÊòØ10‰ΩçÊàñ13‰ΩçÊï∞Â≠ó';
          return;
        }
      }
      this.error = '';
    },
    handleSearch() {
      const initialInput = this.searchInput.trim();
      if (!initialInput) {
        this.error = 'ËØ∑ËæìÂÖ•ÊêúÁ¥¢ÂÜÖÂÆπ';
        return;
      }

      // Ê∏ÖÈô§‰πãÂâçÁöÑÈò≤ÊäñÂÆöÊó∂Âô®
      if (this.debounceTimeout) {
        clearTimeout(this.debounceTimeout);
        console.log('Â∑≤Ê∏ÖÈô§‰πãÂâçÁöÑÈò≤ÊäñÂÆöÊó∂Âô®');
      }

      // Â¶ÇÊûúÂ≠òÂú®ËøõË°å‰∏≠ÁöÑËØ∑Ê±ÇÔºåÂÖàÂèñÊ∂à
      if (this.currentRequest) {
        this.currentRequest.cancel('Êñ∞ÁöÑÊêúÁ¥¢ËØ∑Ê±Ç');
        console.log('Â∑≤ÂèñÊ∂à‰πãÂâçÁöÑÊêúÁ¥¢ËØ∑Ê±Ç');
        this.currentRequest = null;
        this.isSearching = false;
      }

      // Á°Æ‰øùÊØèÊ¨°ÊêúÁ¥¢ËØ∑Ê±ÇÈÉΩËÉΩÊ≠£Á°ÆÂ§ÑÁêÜ
      if (this.isSearching) {
        console.log('ÂΩìÂâçÊ≠£Âú®ÊêúÁ¥¢ÔºåÂøΩÁï•Êñ∞ÁöÑÊêúÁ¥¢ËØ∑Ê±Ç');
        return;
      }

      // ËÆæÁΩÆÊñ∞ÁöÑÈò≤ÊäñÂÆöÊó∂Âô®
      this.debounceTimeout = setTimeout(async () => {
        try {
          const currentInput = this.searchInput.trim();
          if (!currentInput) return;

          // ÂàõÂª∫Êñ∞ÁöÑÂèØÂèñÊ∂àËØ∑Ê±Ç
          const cancelToken = axios.CancelToken.source();
          this.currentRequest = cancelToken;
          this.isSearching = true;
          
          this.$emit('search-start');
          console.log('ÂºÄÂßãÊêúÁ¥¢:', currentInput);
          
          // ‰ΩøÁî®ÊúÄÊñ∞ËæìÂÖ•ÂÄºÂèëËµ∑ËØ∑Ê±Ç
          await this.$emit('search', currentInput, cancelToken.token);
          
          console.log('ÊêúÁ¥¢ÊàêÂäü:', currentInput);
        } catch (error) {
          // Áªü‰∏ÄÂ§ÑÁêÜÂèñÊ∂àËØ∑Ê±ÇÁöÑÊÉÖÂÜµ
          if (axios.isCancel(error)) {
            console.log('ËØ∑Ê±ÇÂèñÊ∂à:', error.message);
            return;
          }
          
          console.error('ÊêúÁ¥¢ÈîôËØØ:', error);
          let errorMessage = 'ÊêúÁ¥¢ËØ∑Ê±ÇÂ§±Ë¥•ÔºåËØ∑ÈáçËØï';
          
          if (error.response) {
            errorMessage = error.response.data.message || 'ÊúçÂä°Âô®ËøîÂõûÈîôËØØ';
          } else if (error.request) {
            errorMessage = 'Êó†Ê≥ïËøûÊé•Âà∞ÊúçÂä°Âô®';
          }
          
          // ÊòæÁ§∫ÈîôËØØ‰ΩÜ‰∏çÊ∏ÖÁ©∫ËæìÂÖ•
          this.error = `ÊêúÁ¥¢Â§±Ë¥•: ${errorMessage}`;
          this.$emit('search-error', error);
        } finally {
          // ËØ∑Ê±ÇÂÆåÊàêÂêéÊ∏ÖÁêÜ
          this.currentRequest = null;
          this.isSearching = false;
          this.$emit('search-end');
          console.log('ÊêúÁ¥¢ÁªìÊùü');
        }
      }, 50); // ‰ºòÂåñÈò≤ÊäñÊó∂Èó¥
    },
    
    beforeUnmount() {
      // ÁªÑ‰ª∂Âç∏ËΩΩÊó∂Ê∏ÖÁêÜ
      if (this.debounceTimeout) {
        clearTimeout(this.debounceTimeout);
      }
      if (this.currentRequest) {
        this.currentRequest.cancel('ÁªÑ‰ª∂Âç∏ËΩΩ');
      }
    },
  }
};
</script>

<style scoped>
.search-bar {
  max-width: 800px;
  margin: 0 auto;
}

.card {
  border: none;
  background: #fff;
  transition: all 0.3s ease;
}

.card:hover {
  transform: translateY(-2px);
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15) !important;
}

.btn-primary {
  padding-left: 2rem;
  padding-right: 2rem;
}

.form-control:focus {
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
  border-color: #80bdff;
}
</style>